<template>
	<div id="addStaff">
		<div class="page_title">
			<router-link :to="{path:'/shop/staff'}">
				<span>员工管理</span>
			</router-link>
			<span class="blue">></span>
			<span class="blue">编辑员工信息</span>
		</div>
		<div class="staffCard">
			<div class="form_title">编辑员工信息</div>
			<div class="from_card">
				<el-form ref="form" :model="form1" label-width="100px" size="mini" :rules="rules">
					<el-form-item label="店铺名称">
						<span>新吃货美食店</span>
					</el-form-item>
					<el-form-item label="职位" prop="duty">
						<el-select v-model="form1.duty" placeholder="职位">
							<el-option label="0" value="cunshang"></el-option>
							<el-option label="1" value="dage"></el-option>
						</el-select>
						<router-link :to="{path:'/staff/editDuty',query:{infor:allDutyName}}">
							<el-button class="add-btn">新增职位</el-button>
						</router-link>
					</el-form-item>
					<el-form-item label="部门" prop="department_id">
						<el-select v-model="form1.department_id" placeholder="部门">
							<el-option v-for="item in allDepartName" :label="item" :value="item" :key="item"></el-option>
						</el-select>
						<el-button class="add-btn">新增部门</el-button>
					</el-form-item>
					<el-form-item label="登录手机号">
						<el-input v-model="inputPhone"></el-input>
					</el-form-item>
					<el-form-item label="登录验证码">
						<el-input v-model="inputTest"></el-input>
						<el-button class="add-btn" @click="getTestNum">获取验证码</el-button>
					</el-form-item>
				</el-form>
				<div class="getInfor">
					<el-button @click="getCountInfor" v-if="success">读取账号信息</el-button>
					<el-button @click="getCountInfor" v-else>读取账号信息失败</el-button>
				</div>
				<div class="inforCard" v-if="GetInfo">
					<el-form :model="form" label-width="100px" size="mini">
						<el-form-item label="姓名">
							<span>{{form.real_name}}</span>
						</el-form-item>
						<el-form-item label="身份证">
							<span>{{form.identity}}</span>
						</el-form-item>
						<el-form-item label="性别">
							<span>{{form.sex}}</span>
						</el-form-item>
						<el-form-item label="健康证扫描件">
							<img class="img" src="../image/emptypic_pic_normal.png" />
						</el-form-item>
						<el-form-item label="绑定微信">
							<span>{{form.is_weixin}}</span>
						</el-form-item>
					</el-form>
				</div>
				<div class="btnGroup">
					<el-button class="saveBtn" @click="saveInvitStaff('form')">保存</el-button>
					<el-button class="cancelBtn" @click="canselInvitStaff('form')">取消</el-button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import { staffDataGet } from "../../api";
	import { treeMethods } from "../../index";
	export default {

		data() {
			return {
				inputPhone: "",
				inputTest: "",
				allDepartName: [],
				allDutyName: [],
				form1: {},
				form: {},
				success: true,
				GetInfo: false,
				rules: {
					duty: [{
						required: true,
						message: '请选择职位',
						trigger: 'click'
					}],
					department_id: [{
						required: true,
						message: '请选择部门',
						trigger: 'click'
					}]
				}
			}
		},
		created() {
			this.getAllDepartName();
		},
		methods: {
			//获取验证码
			getTestNum() {
				if(this.inputPhone !== "") {
					let data = {
						get_phone_code: 1,
						phone: this.inputPhone
					}
					staffDataGet.getPhoneTest(data, (resp) => {
						this.inputTest = resp.data.phone_code
					})
				} else {
					alert("请输入电话号码");
					return;
				}

			},
			//获取账号信息
			getCountInfor() {
				if(this.inputPhone !== "" && this.inputTest !== "") {
					let data = {
						get_user_info: 1,
						phone: this.inputPhone,
						phone_code: this.inputTest
					}
					staffDataGet.getUserInfor(data, (resp) => {
						console.log(resp)
						if(resp.ret === 0) {
							this.GetInfo = true;
							this.form = resp.data.user_info;
						} else {
							this.GetInfo = false;
							this.success = false;
						}
					})
				} else {
					alert("请填写登录手机号以及获得的验证码")
				}
			},
			//获取部门列表
			getAllDepartName() {
				let data = {
					get_department_list: 1
				}
				staffDataGet.getDepartList(data, (resp) => {
					resp.data.department_list.forEach((item) => {
						if(this.allDepartName.indexOf(item.department_name) == -1) {
							this.allDepartName.push(item.department_name)
						}
					})
				})
			},
			//获取职位列表
			getAllDutyName() {

			},
			//保存添加员工
			saveInvitStaff(formName) {
				this.$refs[formName].validate(valid => {
					if(valid) {
						if((this.form && this.form !== undefined) && (this.inputPhone !== "" && this.inputTest !== "")) {
							let data = {
								shop_employee_save: 1,
								userid: this.form.userid,
								phone: this.inputPhone,
								department_id: this.form1.department,
								duty_id: this.form1.duty,
								real_name: this.form.real_name
							}
							staffDataGet.getUserInfor(data, this.goBack())
						} else {
							alert("请填写完整信息")
						}
					}
				})
			},
			//取消添加员工
			canselInvitStaff(formName) {
				this.$refs[formName].resetFields();
				this.goBack()
			},
			//返回员工管理页面
			goBack() {
				this.$router.push("/shop/staff")
			}

		}
	}
</script>

<style lang="scss" scoped>
	@import "src/styles/mixin.scss";
	.staffCard {
		background: #ffffff;
		box-shadow: 0 2px 4px 0 #becaeb;
	}
	
	.from_card {
		width: 50%;
		padding: 48px 0 40px 0;
		margin-left: 133px;
	}
	
	.inforCard {
		width: 60%;
		margin-bottom: 30px;
		background: #F6F8FC;
	}
	
	.page_title {
		@include wh(100%, 16px);
		@include fc(12px, #333333);
		margin-bottom: 14px;
	}
	
	.form_title {
		@include wh(100%, 40px);
		@include fc(14px, #666666);
		line-height: 40px;
		background-color: #F6F8FC;
		padding-left: 14px;
	}
	
	.img {
		width: 100px;
		height: 50px;
	}
	
	.pinpo {
		font-size: 12px;
		color: #9B9B9B;
	}
	
	.add-btn {
		margin: 0 14px;
		padding: 0 5px;
	}
	
	.blue {
		font-size: 12px;
		color: #4877E7;
	}
	
	.btnGroup {
		margin-top: 100px;
		padding-left: 230px;
	}
	
	.getInfor {
		padding-left: 20%;
		margin-bottom: 10px;
	}
</style>
<style lang="scss">
	@import "src/styles/mixin.scss";
	#addStaff {
		.saveBtn.el-button {
			@include wh(100px, 34px);
			background: #4877e7;
			color: #FFFFFF;
		}
		.cancelBtn.el-button {
			@include wh(100px, 34px);
		}
		.getInfor .el-button {
			min-width: 120px;
		}
		.el-button {
			@include wh(80px, 30px);
			border: 1px solid #4877e7;
			color: #4877e7;
			text-align: center;
			border-radius: 5px;
			font-size: 12px;
		}
		.el-input {
			width: 220px;
		}
		.inforCard {
			.el-form-item {
				margin-bottom: 5px;
			}
		}
	}
</style>


